@charset "UTF-8";
/*!
Theme Name: Finara
Author: Irshman
Description: Theme for test task
Version: 1.0.0
Requires PHP: 8.0
Text Domain: finara
 */
/* Скидання базових стилів для всіх елементів */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 100%;
  line-height: 1.5;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: normal;
  font-size: inherit;
  line-height: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}
a:hover, a:focus, a:active {
  text-decoration: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}

td,
th {
  padding: 0;
  border: 0;
  text-align: left;
  font-weight: normal;
}

img,
picture,
video,
canvas,
svg {
  max-width: 100%;
  display: block;
  height: auto;
}

input,
button,
textarea,
select {
  font: inherit;
  color: inherit;
  background: none;
  border: none;
}
input:focus,
button:focus,
textarea:focus,
select:focus {
  outline: none;
}

button {
  cursor: pointer;
  border: 0;
  background-color: transparent;
  appearance: none;
  padding: 0;
}

iframe {
  border: 0;
  display: block;
}

:focus-visible {
  outline: 2px solid currentColor;
  outline-offset: 2px;
}

@keyframes shine {
  0% {
    left: -100px;
  }
  20% {
    left: 100%;
  }
  100% {
    left: 100%;
  }
}
body {
  font-family: var(--font-content-family);
  height: 100%;
  overflow-x: hidden;
  background-color: #f9f8fe;
  -ms-overflow-style: none;
  scrollbar-width: none;
  -webkit-overflow-scrolling: touch;
}
body::-webkit-scrollbar {
  display: none;
}
body.active {
  overflow: hidden;
}

main#primary {
  min-height: 420px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: contain;
}

.container {
  max-width: 82.5rem;
  padding: 0 0.375rem;
  box-sizing: content-box;
  margin: 0 auto;
}

.site {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%;
}
.site main {
  flex: 1 0 auto;
}
.site footer {
  flex: 0 0 auto;
}

.btn {
  color: #fff;
  background-color: #ee2264;
  border-radius: 1.6875rem;
  padding: 0.5rem 1.25rem;
  font-size: 0.8125rem;
  transition: background-color 0.3s linear;
}
@media (hover: hover) {
  .btn:hover {
    background-color: #f7f6fe;
    color: #383836;
  }
}

@media (min-width: 81.24875em) {
  .btn {
    padding: 0.5rem 2.5rem;
  }
}
.header {
  padding: 0.625rem 0;
}
.header__content {
  background-color: #fff;
  border-radius: 1.6875rem;
  box-shadow: 0px 8px 14px 0px rgba(38, 38, 38, 0.07);
  padding: 0.375rem 1.5625rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 3.375rem;
}
.header__logo {
  max-width: 9.6875rem;
}
.header__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__menu {
  display: none;
}
.header__button {
  display: none;
}
.header__phone {
  font-size: 0.875rem;
  color: rgb(56, 56, 54);
  position: relative;
  padding: 0 1.5625rem 0 0.9375rem;
}
.header__phone:before {
  content: "";
  position: absolute;
  left: 0;
  top: -50%;
  transform: translateY(0%);
  height: 2.6875rem;
  width: 0.0625rem;
  background-color: rgb(247, 246, 254);
}
.header__burger {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  width: 1.5625rem;
  height: 0.9375rem;
  position: relative;
  cursor: pointer;
  z-index: 11;
}
.header__burger span {
  display: block;
  width: 100%;
  height: 0.125rem;
  background-color: #000;
}
.header__burger span:last-child {
  width: 80%;
}
.header__burger.active span {
  position: relative;
  transition: all 0.3s linear;
}
.header__burger.active span:nth-child(2) {
  transform: rotate(-45deg);
  bottom: 0.375rem;
}
.header__burger.active span:first-child {
  transform: rotate(45deg);
  top: 0.375rem;
}
.header__burger.active span:last-child {
  display: none;
}

.menu-h {
  background-color: #fff;
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  top: -200%;
  z-index: 10;
  height: 100vh;
}
.menu-h.active {
  top: 0;
}
.menu-h .menu {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 0.3125rem;
  font-size: 1.9375rem;
}
.menu-h .menu .current-menu-item {
  color: #ee2264;
}

@media (min-width: 47.99875em) {
  .header__burger {
    visibility: hidden;
    display: none;
  }
  .header__phone:before {
    display: none;
  }
  .header__button {
    display: flex;
  }
  .menu-h {
    position: static;
    height: unset;
    background: transparent;
  }
  .menu-h .menu {
    flex-direction: row;
    column-gap: 0.625rem;
    color: #383836;
    font-size: 0.75rem;
  }
  .menu-h .menu .menu-item {
    position: relative;
  }
  .menu-h .menu .menu-item:hover {
    color: #000;
  }
  .menu-h .menu .sub-menu {
    display: none;
    border-radius: 0.625rem;
    background-color: #fff;
    padding: 0.9375rem;
    position: absolute;
    top: 1.875rem;
    box-shadow: 0px 8px 14px 0px rgba(38, 38, 38, 0.07);
  }
  .menu-h .menu .menu-item-has-children {
    position: relative;
  }
  .menu-h .menu .menu-item-has-children::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 30px;
  }
  .menu-h .menu .menu-item-has-children:hover .sub-menu {
    display: flex;
  }
}
@media (min-width: 81.24875em) {
  .menu-h .menu {
    column-gap: 3.75rem;
  }
}
.form {
  display: flex;
  flex-direction: column;
  row-gap: 0.75rem;
}
.form__wrap {
  display: flex;
  flex-direction: column;
  row-gap: 0.75rem;
}
.form__input {
  border: 0.0625rem solid rgb(247, 246, 254);
  border-radius: 0.3125rem;
  padding: 0.625rem;
  width: 100%;
}
.form__input input {
  width: 100%;
}
.form__input input::placeholder {
  color: #6e6e6e;
  font-size: 0.75rem;
  font-weight: lighter;
}
.form__input input:focus {
  outline: none;
}
.form__input input:-webkit-autofill,
.form__input input:-webkit-autofill:hover,
.form__input input:-webkit-autofill:focus,
.form__input input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #fff inset !important;
}
.form .cvr {
  display: none;
}
.form__submit {
  color: #fff;
  background-color: #ee2264;
  border-radius: 1.6875rem;
  padding: 0.9375rem;
  margin-top: 0.75rem;
}

.dropdown {
  border: 0.0625rem solid rgb(247, 246, 254);
  border-radius: 0.3125rem;
  padding: 0.625rem;
  transition: all 0.3s ease;
  position: relative;
}
.dropdown summary {
  color: #6e6e6e;
  font-size: 0.75rem;
  font-weight: lighter;
  cursor: pointer;
  list-style: none;
  position: relative;
}
.dropdown summary::-webkit-details-marker {
  display: none;
}
.dropdown summary::after {
  content: "›";
  position: absolute;
  right: 0;
  top: -50%;
  font-size: 1.5625rem;
  transform: rotate(90deg);
  transition: transform 0.3s ease;
}
.dropdown[open] summary::after {
  transform: rotate(-90deg);
}
.dropdown__body {
  position: absolute;
  top: 115%;
  left: 0;
  background-color: #fff;
  border-radius: 1.0625rem;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.06);
  padding: 1.5625rem 1.875rem;
  width: 100%;
  z-index: 5;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0.625rem;
}

.checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 0.75rem;
  position: relative;
  padding-left: 2.1875rem;
  user-select: none;
  color: #3c3c3c;
  margin-bottom: 1.25rem;
}
.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
.checkbox .checkmark {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 1.25rem;
  width: 1.25rem;
  border-width: 1px;
  border-color: rgb(247, 246, 254);
  border-style: solid;
  border-radius: 5px;
  background-color: rgb(247, 246, 254);
  transition: all 0.3s ease;
  cursor: pointer;
}
.checkbox .checkmark::after {
  content: "";
  position: absolute;
  display: none;
  left: 0.375rem;
  top: 0.1875rem;
  width: 0.3125rem;
  height: 0.625rem;
  border: solid #ee2264;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.checkbox input:checked ~ .checkmark::after {
  display: block;
}

@media (min-width: 47.99875em) {
  .form__wrap {
    flex-direction: row;
    justify-content: space-between;
    column-gap: 0.75rem;
  }
  .form .cvr {
    display: flex;
  }
  .form__submit {
    width: 100%;
    max-width: 13.125rem;
    margin-left: auto;
  }
  .dropdown__body {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 81.24875em) {
  .dropdown__body {
    grid-template-columns: repeat(3, 1fr);
  }
}
